.ProfileFrens{
    color: rgb(0, 0, 0);
    font-size:20px;
    padding: 10px;
    background-color: rgb(255, 255, 255);
    grid-area: F;
    border-radius: 4px;
    
}
.ProfileFrens__data{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    
    grid-row-gap: 10px;
    text-align: center;
    
}
.Userimg img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.Frends{
    margin: 20px 0px 0px 0px;
    font-size: 14px;
    overflow: hidden;
    width: 70px;    

}