.Posts{
    color: rgb(0, 0, 0);
    font-size: 28px;
    padding: 10px;
    grid-area: P;
    margin: 10px 0px;
    background-color: rgb(255, 255, 255);
    
}
.texterea{
    width: 520px;
    height: 40px;
    border-radius: 4px;
    resize: none;
}
.texterea:hover{
    box-shadow: 1px 1px 10px rgb(140, 140, 141);
}
.texterea:focus{
    box-shadow: 1px 1px 10px rgb(72, 102, 184);
    outline:none;
    border: 1px solid rgb(119, 119, 119);
}
.buttons{
    text-align: right;
}
.buttons button{
    color: rgb(255, 255, 255);
    width: 120px;
    height: 25px;
    background-color: #2a5885b2;
    border: none;
    border-radius: 4px;
    margin: 0px 0px 10px 0px;
}
.buttons button:hover{
    background-color: #2a5885ce;
}
.Posts__content{
    display:flex;
    flex-direction:column-reverse;

}
