.ProfileContacts{
    display: grid;
    grid-template-columns: 2fr 4fr;
    color: #2a5885;
    font-size: 24px;
    font-weight: 400;
}
.ProfileContacts__span{
    
    font-size: 15px;
    color: #818c99;
}
.ProfileContacts__button{
    border-bottom: 1px solid rgb(177, 177, 177);
    text-align: right;
    margin: 0px 0px 10px 0px;
    
}
.ProfileContacts__button button {
    color: rgb(255, 255, 255);
    background-color: #2a5885bb;
    width: 120px;
    height: 24px;
    border: none;
    border-radius: 4px;
    margin: 0px 0px 2px 0px;
    
}
.ProfileContacts__button button:hover {
    background-color: #3c6791ec;
    
}
.disabledBut{
    opacity: 0;
}
