.ProfileContacts{
    display: grid;
    grid-template-columns: 2fr 4fr;
}
.userId{
    display: none;
}
.ProfileContacts__span{
    font-size: 15px;
    color: #818c99;
}
.ProfileContacts__form{
    color: #2a5885;
    font-size: 24px;
    font-weight: 400;
    margin: 4px 10px;
    border-radius: 4px;
    border: 1px solid rgb(124, 123, 123);
    outline:none
    
}
.ProfileContacts__form:hover{
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
    border: 1px solid rgb(255, 255, 255);
}
.ProfileContacts__button{
    border-bottom: 1px solid rgb(177, 177, 177);
    text-align: right;
    margin: 0px 0px 10px 0px;
    
}
.ProfileContacts__button button {
    color: rgb(255, 255, 255);
    background-color: #2a5885bb;
    width: 120px;
    height: 24px;
    border: none;
    border-radius: 4px;
    margin: 0px 0px 2px 0px;
}
.ProfileContacts__button button:hover {
    background-color: #3c6791ec;
    
}
