.Profile{
    color: rgb(0, 0, 0);
    font-size: 30px;
    display: grid;
    grid-template-areas:"PM  PI";
    grid-template-columns: 1.7fr 4fr;
    grid-column-gap:12px;
    
}

.Profile__mein{
    grid-area: PM;
    display: flex;
    flex-direction: column;
}
.Profile__info{
    grid-area: PI;
    display: flex;
    flex-direction: column;
}