
.wrapper_vc{
    background-color:#edeef0;
    height: 1200px;
    
    
}
.mein__greed{
    display: grid;
    grid-template-areas:"N" "M" ;
    grid-template-columns: 2fr 10fr;
    grid-column-gap: 6px;
    max-width: 970px;
    margin: 0 auto;
    padding: 14px;
 
}

.nav__bar{
    grid-area: N;
}

.mein_content{
    grid-area: M;
}