.input {
    border: 1px solid rgba(41, 61, 114, 0.4);
    background-color: rgba(132, 167, 221, 0.2);
    width: 250px;
    border-radius: 4px;
    padding: 10px 15px;
    margin: 0 auto 10px auto;
    text-align: center;
    font-size: 18px;
    color: rgb(2, 2, 2);
  }
.buttons{
    display: flex;
    flex-direction: column;
}
.buttons button{
    color: rgb(255, 255, 255);
    width: 200px;
    height: 30px;
    background-color: #2a5885b2;
    border: none;
    border-radius: 4px;
    margin: 0px 0px 10px 0px;
}
.buttons button:hover{
    background-color: #2a5885ce;
}
  