.Users{
    color: rgb(0, 0, 0);
    font-size: 30px;
    padding: 10px;
    background-color: rgb(255, 255, 255);
    border-radius: 4px;
}
.UsersPage{
    color: rgb(155, 155, 155);
    margin: 0px 20px 20px 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(145, 143, 143);
    display: flex;
    justify-content:space-between;
    
}
.UsersPage:hover{
    border-bottom: 1px solid rgb(80, 79, 79);
}
.UsersPage span:hover{
    color: rgb(6, 47, 228);
}
.totalUsersCount{
    font-size: 22px;
    margin-left: 20px;
}
.UserData{
    display: grid; 
    grid-template-columns: 1fr 4fr;
    font-size: 20px;
}
.Userimg{
    width: 80px;
    height: 80px;
    text-align: center;
    margin: 10px 0px 10px 20px;
}
.Userimg img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.UserName{
    padding: 4px 0px 0px 0px;
    font-size: 22px;
}
.UserStatus{
    font-size: 18;
}
.activPage{
    color: #2a5885fd;
}
.divButton{
    text-align: left;
    margin: 6px 0px 0px 0px;
   
}
.Button{
    color: rgb(255, 255, 255);
    width: 200px;
    height: 30px;
    background-color: #2a5885b2;
    border: none;
    border-radius: 4px;
}
.Button:hover{
    background-color: #2a5885ce;
}
.portionNumber{
    color: rgb(255, 255, 255);
    width: 60px;
    height: 25px;
    background-color: #2a5885b2;
    border: none;
    border-radius: 4px;
    margin: 8px 0px 0px 0px;
}
.portionNumber:hover{
    background-color: #2a5885ce;
} 
.portionNumber:disabled{
    background-color: #b3b5b8ce;
} 
 
